body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    background-color: rgb(210, 224, 250);
    padding: 10px;
}

.h1 {
    color: brown;
    text-align: center;
    text-justify: center;
}

.container {
    flex: auto;
    max-width: 200px;
    width: 100%;
    background-color: lightgrey;
    height: 280px;
    text-align: center;
    padding: 40px;
    margin: auto;
    margin-bottom: 30px;
    border-radius: 25px;
}

.height {
    font-size: 15px;
    font-weight: 500;
    color: black;
}

.weight {
    font-size: 15px;
    font-weight: 500;
    color: black;
}

.input {
    width: auto;
    padding: 0 5px;
    height: 5px;
    font-size: 25px;
}

label {
    position: relative;
    left: 5px;
    font: 15px;
}

button {
    background-color: green;
    text-align: center;
    justify-content: center;
    color: white;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    outline: none;
}

button:hover {
    color: red;
    cursor: pointer;
}

.incenter {
    background-color: lightgoldenrodyellow;
    text-align: center;
    margin: auto;
    border-radius: 25px;
    font-size: 15px;
}
